.contact{
    display: flex;
    height:100vh;
    color: white;
}

a{
    text-decoration: none;
    color: white;
}

.c-left{
    flex: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}
.c-left-wrapper{
    padding:5%;
    height: 70%;
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: space-between;
    background-color: rgb(155, 16, 53);
    border-radius: 20px;
    box-shadow:-5px 5px 30px 1px rgb(46, 46, 46);
}

.contact-desc{
    font-size: 25px;
}
.contact-title{
    font-size: 60px;
}




.c-info-item{

    width: 80%;
    display: flex;
}
.c-info-item-l{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.c-info-item-r{
    flex: 3;
    display: flex;
    align-items: center;
    font-size: 20px;
}
.c-icon{
    width: 40px;
    height: 40px;
}


.c-right{
    flex: 1.5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.c-right-wrapper{
    height: 90%;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.c-img{
    width: 90.5%;
    object-fit: cover;
}


@media screen and (max-width: 1200px){
    .c-left-wrapper{
        line-height: 40px;
        align-items: flex-start;
        justify-content: space-between;
    }

    .c-info-item{
        width: 100%;
        align-items: center;
        flex-direction: column;
    }
}

@media screen and (max-width: 700px){

    .c-left{
        background-color: rgb(0, 0, 0);
        flex: 1;
        /* background-image: url("https://www.pinclipart.com/picdir/big/113-1134990_pink-clipart-email-email-logo-png-pink-transparent.png");
        background-size: 100%;
        background-repeat: no-repeat; */
    }
    .c-right{
        flex: 0;
    }

    .c-left-wrapper{
        line-height: 40px;
        align-items: flex-start;
        justify-content: space-between;
    }

    .c-info-item{
        width: 100%;
        flex-direction: column;
    }

    .c-info-item-l{
        justify-content: center;
        align-items: flex-end;
    }

    .c-info-item-r{
        justify-content: center;
        align-items: flex-end;
    }
    .contact-title{
        font-size: 60px;
    }
    .contact-desc{
        font-size: 30px;
    }
}

