
.about{
    height: 100vh;
    display:flex;
}

.a-title{
    font-size: 50px;
    font-weight: bold;
    margin-bottom: 20px;
    /*margin-top:  10%;*/
    /*color: #*/;
}

.a-left{
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /*color: rgb(231, 34, 86);*/
}
.a-left-wrapper{
    width: 80%;
    height: 80%;
    display: flex;
    align-items: left;
    flex-direction: column;
    justify-content: space-between;
}

.a-education{
    display: flex;
    width: 100%;
    height: 25%;
    background-color: rgb(207, 25, 73);
    box-shadow: -5px 5px 30px 1px rgb(46, 46, 46);
    /*color: black;*/
    border-radius: 20px;
    overflow: hidden;
    font-size: 20px;
}

.a-edu-icon{
    flex: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.a-edu-data{
    flex: 80%;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    line-height: 25px;
}


.a-right{
    flex: 1.5;
    display: flex;
    align-items: center;
    justify-content: center;
}
.a-right-wrapper{
    width: 80%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: aliceblue; */
}


.a-img{
    width: 100%;
    object-fit: cover;
}

.a-icon{
    width: 60px;
    height: 60px;
}

@media screen and (max-width: 1020px) {
    .a-img{
        display: none;
    }    

    .a-right{
        flex: 0;
    }

    .a-left{
        flex: 1;
    }

    .a-left-wrapper{
        background-image:url('https://www.pinclipart.com/picdir/big/529-5292774_student-graduation-ceremony-silhouette-square-academic-silhouette-clipart.png');
        background-size: cover;
        opacity: 0.8;
    }
    .a-education{
        opacity: 0.8;
    }

}
